<template>
  <section class="main-sidebar">
    <aside>
      <div class="view-sidebar">
        <div @click="triggerMenu" ref="menuBox" class="btn_mb">
          <hr>
          <hr>
          <hr>
        </div>
        <ul class="sidebar" ref="sidebar">
          <li class="sidebar-item" v-for="(key, val) in route" :key="val">
            <a>{{$t(`sidebar.${val}`)}}</a>
            <ul class="sidebar-item">
              <li v-for="(item) in key" :key="item.path"
                  :class="{'active': item.path === $route.path}">
                <router-link :to="{path: item.path}">{{$t(item.text)}}</router-link>
              </li>
            </ul>
          </li>
        </ul>
        <viewLinkorg></viewLinkorg>
      </div>
    </aside>
  </section>
</template>

<script>
const viewLinkorg = () => import(/* webpackChunkName: "header" */ '../view/view');
const router = {
  dev: [{
    path: '/view/installation',
    text: 'sidebar.install',
  }, {
    path: '/view/quickstart',
    text: 'sidebar.quick',
  }, {
    path: '/view/i18n',
    text: 'sidebar.i18n',
  }],
  component: [{
    path: '/view/button',
    text: 'sidebar.button',
  }, {
    path: '/view/input',
    text: 'sidebar.input',
  }, {
    path: '/view/ipInput',
    text: 'sidebar.ipInput',
  }, {
    path: '/view/subnetInput',
    text: 'sidebar.subnetInput',
  }, {
    path: '/view/switch',
    text: 'sidebar.switch',
  }, {
    path: '/view/message',
    text: 'sidebar.msg',
  }, {
    path: '/view/vtip',
    text: 'sidebar.tip',
  }],
};
export default {
  name: 'sidebar',
  data() {
    return {
      route: router,
    };
  },
  watch: {
    '$route'() {
      if (document.querySelector('body').clientWidth <= 884) {
        this.triggerMenu()
      }
    }
  },
  components: {
    viewLinkorg,
  },
  methods: {
    triggerMenu() {
      this.$refs.menuBox.classList.toggle('active');
      this.$refs.sidebar.classList.toggle('barActive');
    },
  },
  mounted() {
  },
};
</script>

<style lang="sass" scoped>
  @import "../../style/sidebar"
</style>
