<script>
  import ie from '../../untils/ie'
export default {
  name: 'HeaderLinkorg',
  data() {
    return {
      result: '',
      logo: null,
      logoBox: null,
    };
  },
  methods: {
    changeLanguage(command) {
      this.$i18n.locale = command;
    },
    changeVersion() {
      // command
    },
    querySearchAsync() {

    },
    handleSelect() {

    },
    changeDomStyle(node, style = {}) {
      if (!node) return false;
      for (let [key, value] of Object.entries(style)) {
        node.style[key] = value;
      }
    },
    winScroll(e) {
      if (document.querySelector('body').clientWidth > 884) {
        return false
      }
      !this.logo && (this.logo = document.querySelector('.logo'));
      !this.logoBox && (this.logoBox = document.querySelector('#logo-box'));
      if ((document.body.scrollTop || document.documentElement.scrollTop) >= 52) {
        this.changeDomStyle(this.logo, {
          width: '120px',
          height: '40px',
        });
        this.changeDomStyle(this.logoBox, {
          width: '100%',
          height: '46px',
          'box-shadow': '0 2px 3px #e8e8e8',
        })
      } else {
        this.changeDomStyle(this.logo, {
          width: '',
          height: '',
        });
        this.changeDomStyle(this.logoBox, {
          width: '',
          height: '',
          'box-shadow': '',
        })
      }
    }
  },
  mounted() {
    document.body.onscroll = this.winScroll;
    let ieVersion = ie();
    if (ieVersion <= 11 && ieVersion !== -1 && ieVersion !== 'edge') {
      // 兼容IE animation-fill-mode
      Array.from(document.querySelectorAll('.logo path')).forEach(item => {
        item.style.strokeDashoffset = '0px';
      });
    }
  }
};
</script>

